			/**
			 * @ngdoc service
			 * @name WcResponsiveTableModule.service:WcTableScopeService
			 * @description
			 * The WcTableScopeService is responsible for maintain the scope of the table, the service holds the methods
			 * that are related to table scope.
			 */

'use strict';

angular.module('WcResponsiveTableModule')
	.service('WcTableScopeService',
		function() {
			var parentScope = null;
			var tableScope = null;

			/**
			 * @ngdoc method
			 * @name getParentScope
			 * @methodOf WcResponsiveTableModule.service:WcTableScopeService
			 * @returns {string} This string holds parent scope value.
			 * @description
			 * The method is to get the parent scope value
			 */

			this.getParentScope = function() {
				return parentScope;
			};

			/**
			 * @ngdoc method
			 * @name getTableScope
			 * @methodOf WcResponsiveTableModule.service:WcTableScopeService
			 * @returns {string} This string holds table scope value.
			 * @description
			 * The method is to get the table scope value
			 */

			this.getTableScope = function() {
				return tableScope;
			};

			/**
			 * @ngdoc method
			 * @name setParentScope
			 * @methodOf WcResponsiveTableModule.service:WcTableScopeService
			 * @param {string} newParentScope This string value holds the new parent scope
			 * @description
			 * The method is to set the parent scope value
			 */

			this.setParentScope = function(newParentScope) {
				if(newParentScope.$id !== 0 && !newParentScope.$id) {
					parentScope = null;
					console.error('WcTableScopeService: setTableScope(); You must pass in a valid Angular Scope');
				} else if(tableScope && newParentScope.$id === tableScope.$id) {
					parentScope = null;
					console.error('WcTableScopeService: setTableScope(); parent scope and table scope can not be the same scope');
				} else {
					parentScope = newParentScope;
				}
			};

			/**
			 * @ngdoc method
			 * @name setTableScope
			 * @methodOf WcResponsiveTableModule.service:WcTableScopeService
			 * @param {string} newTableScope This string value holds the new table scope
			 * @description
			 * The method is to set the table scope value
			 */

			this.setTableScope = function(newTableScope) {
				if(newTableScope.$id !== 0 && !newTableScope.$id) {
					tableScope = null;
					console.error('WcTableScopeService: setTableScope(); You must pass in a valid Angular Scope');
				} else if(parentScope && newTableScope.$id === parentScope.$id) {
					tableScope = null;
					console.error('WcTableScopeService: setTableScope(); parent scope and table scope can not be the same scope');
				} else {
					tableScope = newTableScope;
				}
			};
		}
);