/**
 * @ngdoc directive
 * @name WcResponsiveTableModule.directive:wcRowSelectAll
 * @restrict A
 * @description
 * The wcRowSelectAll directive is responsible for selecting all rows of the datatable on a single click.
 */

'use strict';

angular.module('WcResponsiveTableModule').directive('wcRowSelectAll', function() {
	return {
		restrict: 'A',
		scope: {},
		controller: ['$scope', 'WcTableScopeService', function($scope, WcTableScopeService) {
			$scope.viewScope = WcTableScopeService.getParentScope();
			$scope.selectAll = function() {
				$scope.viewScope.$broadcast('table.select.checkbox.all');
				$scope.viewScope.$broadcast('table.select.button.all');
				$scope.$apply();
			};
		}],
		link: function(scope, elem/*, attrs, ctrl*/) {
			elem.on('click', function () {
				scope.selectAll();
			});
		}
	};
});
