/**
 * @ngdoc directive
 * @name WcResponsiveTableModule.directive:wcColumnSort
 * @restrict A
 * @scope
 * @param {string} wcColumnSort The parameter holds the column sort values
 * @param {string} wcColumnSortDefault The parameter holds the default column sort values
 * @param {string} specialChars The string value holds the special characters
 * @param {string} columnIndex The variable holds the column index value
 * @description
 * The wcColumnSort directive adds column sort option to jquery datatable, this directive holds methods that are
 * related to column sort operation.
 */

'use strict';

angular.module('WcResponsiveTableModule').directive('wcColumnSort', function () {
    return {
        restrict: 'A',
        scope: {
            wcColumnSort: '@',
            wcColumnSortDefault: '@',
            specialChars: '@',
            columnIndex: '@'
        },
        controller: ['$scope', '$timeout', 'WcTableScopeService', 'WcTableCollectionService', 'WcTablePaginationService', function ($scope, $timeout, WcTableScopeService, WcTableCollectionService, WcTablePaginationService) {
            var specialChars = $scope.specialChars;

            $scope.tableScope = WcTableScopeService.getTableScope();
            $scope.sortReverse = false;
            $scope.timeout = $timeout;



            this.collectionLength = WcTableCollectionService.getCollectionLength();
            this.resultsPerPage = WcTablePaginationService.getPaginationDefaults().resultsPerPage;

            $scope.sortCollection = function () {

                WcTableCollectionService.sortEnabled = true;
                //induce a delay to limit the FOUC
                $scope.timeout(function () {
                    var collection = WcTableCollectionService.getCollection();
                    function sortOn(arr, prop, reverse, specialChars) {
                        reverse = reverse ? 1 : -1;

                        if (!specialChars) {
                            return arr.sort(function (a, b) {
                                if (_.get(a, prop) < _.get(b, prop)) {
                                    return 1 * reverse;
                                } else if (_.get(a, prop) > _.get(b, prop)) {
                                    return -1 * reverse;
                                } else {
                                    return 0;
                                }
                            });
                        } else {
                            return arr.sort(function (a, b) {
                                return reverse === 1 ? _.get(b, prop).localeCompare(_.get(a, prop)) : _.get(a, prop).localeCompare(_.get(b, prop));
                            });
                        }
                    }

                    $scope.tableScope.previousSortColumn = $scope.tableScope.activeSortColumn;
                    $scope.tableScope.activeSortColumn = $scope.columnIndex;

                    WcTableCollectionService.setCollection(sortOn(collection, $scope.wcColumnSort, $scope.sortReverse, specialChars), WcTableCollectionService.sortEnabled, WcTableCollectionService.filterEnabled);
                    $scope.tableScope.$broadcast('table.sort.complete', $scope.wcColumnSort, $scope.sortReverse);

                    $scope.sortReverse = !$scope.sortReverse;
                }, 30);
            };

            if ($scope.wcColumnSortDefault) {
                if ($scope.wcColumnSortDefault === 'desc') {
                    $scope.sortReverse = true;
                }
                $scope.sortCollection();
            }
            else {
                $scope.timeout(function () {
                    $scope.tableScope.renderTable();
                }, 30);
            }
        }],
        //jshint unused:true
        link: function (scope, elem, attrs, ctrl) {


            elem.addClass('wc-table-column-sort-heading');

            //if(scope.$parent.ngModel) {
            //include listener for when the sorted column changes
            scope.tableScope.$on('table.sort.complete', function (event, column, reverse) {
                var thSpan = elem.find('span.glyphicon:first-child');
                var columnClass = elem.attr('class').match(/wc-table-column-\d+/)[0];
                //var columnSelector = '.wc-table-column-' + scope.columnIndex;
                if (column === scope.wcColumnSort) {
                    thSpan.toggleClass('glyphicon-sort', false);
                    if (!reverse) {
                        thSpan.toggleClass('glyphicon-sort-by-alphabet', true);
                        thSpan.toggleClass('glyphicon-sort-by-alphabet-alt', false);
                    }
                    else {
                        thSpan.toggleClass('glyphicon-sort-by-alphabet-alt', true);
                        thSpan.toggleClass('glyphicon-sort-by-alphabet', false);
                    }
                    $('.' + columnClass).addClass('wc-table-column-highlight');
                    //elem.addClass('sorted');
                }
                else {
                    thSpan.toggleClass('glyphicon-sort', true);
                    thSpan.toggleClass('glyphicon-sort-by-alphabet glyphicon-sort-by-alphabet-alt', false);
                    $('.' + columnClass).removeClass('wc-table-column-highlight');
                    //elem.removeClass('sorted');
                    scope.sortReverse = false;
                }
            });

            scope.tableScope.$on('table.render.complete', function () { // jshint ignore:line
                var columnClass = elem.attr('class').match(/wc-table-column-\d+/)[0];
                if (elem.hasClass('wc-table-column-highlight')) {
                    $('td.' + columnClass).addClass('wc-table-column-highlight');
                }
                else {
                    $('td.' + columnClass).removeClass('wc-table-column-highlight');
                }
            });

            elem.on('click', function () {
                var collectionLength = ctrl.collectionLength;
                var showLoadingIndicator = false;

                if (ctrl.resultsPerPage <= 25) {
                    if (collectionLength >= 100000) {
                        showLoadingIndicator = true;
                    }
                } else if (ctrl.resultsPerPage <= 75) {
                    if (collectionLength >= 10000) {
                        showLoadingIndicator = true;
                    }
                }
                else {
                    if (collectionLength >= 1000) {
                        showLoadingIndicator = true;
                    }
                }

                if (showLoadingIndicator) {
                    scope.tableScope.tableIsLoading();
                }

                scope.sortCollection();
            });

            //glyphicon-sort-by-alphabet
            //glyphicon-sort-by-alphabet-alt
            if (elem[0].children.length) {
                elem.find('input, select').before('<span class="glyphicon glyphicon-sort"></span>');
            }
            else {
                elem.append('<span class="glyphicon glyphicon-sort"></span>');
            }
            //}
        }
    };
});
