/**
 * @ngdoc directive
 * @name WcResponsiveTableModule.directive:wcRowActionSingle
 * @restrict A
 * @scope
 * @param {string} wcRowActionSingle The parameter holds the single row action values
 * @description
 * The wcRowActionSingle directive is responsible for the single row actions that are carried out in the datatable.
 */

'use strict';

angular.module('WcResponsiveTableModule').directive('wcRowActionSingle', function() {
	return {
		restrict: 'A',
		scope: {
			wcRowActionSingle: '@'
		},
		controller: ['$scope', 'WcTableScopeService', function($scope, WcTableScopeService) {
			$scope.singleAction = function() {
				var rowScope = $scope.$parent;
				var viewScope = WcTableScopeService.getParentScope();

				var data = [].concat(rowScope.rowKey);
				console.log('rowActionSingle: rowScope:', rowScope);
				viewScope.$broadcast($scope.wcRowActionSingle, data);
			};
		}],
		link: function(scope, elem/*, attrs, ctrl*/) {
			elem.on('click', function() {
				scope.singleAction();
			});
		}
	};
});