/**
 * @ngdoc directive
 * @name WcResponsiveTableModule.directive:wcRowRepeat
 * @description
 * The wcRowRepeat directive is responsible for the row repeat operation on a datatable
 */

'use strict';

angular.module('WcResponsiveTableModule').directive('wcRowRepeat', ['$compile', '_', function($compile, _) {
	return {
		controller: ['WcTableScopeService', 'WcTableCollectionService', 'WcTablePaginationService', function(WcTableScopeService, WcTableCollectionService, WcTablePaginationService) {
			this.collection = WcTableCollectionService.getCollection;
			this.tableScope = WcTableScopeService.getTableScope;
			this.rowKey = WcTableCollectionService.getRowKey();

			this.pagination = WcTablePaginationService.getPaginationDefaults;
		}],
		link: function(scope, element, attrs, ctrl) {

			// jshint unused:true
			var elements = [];
			var parentElement = element.parent();

			var numberOfCols = scope.numberOfCols;
			var noRecordRow = $('<tr><td class="text-center" colspan='+numberOfCols+' >No record found</td></tr>');


			scope.$on('table.render.start', function() {
				insertRows(ctrl.pagination().pageStart, ctrl.pagination().pageEnd);
				ctrl.tableScope().tableRendered();
			});

			function insertRows(pageStart, pageEnd) {
				var block;
				var childScope;
				var elementsLength = elements.length;
				var index;

				var ngModel = ctrl.tableScope().ngModel;
				var collection = ctrl.collection();

				if(elementsLength > 0) {
					for (index = 0; index < elementsLength; index++) {
						elements[index].el.remove();
						elements[index].scope.$destroy();
					}
					elements = [];
				}

				//TODO make setCollection update pagination instead
				if(collection.length < pageEnd) pageEnd = collection.length;

				for (index = pageStart; index < pageEnd; index++) {
					childScope = scope.$new(false, ctrl.tableScope());
					childScope.row = collection[index];
					childScope.rowKey = childScope.row[ctrl.rowKey];
					childScope.index = index;

					if(ngModel.length > 0) {
						var alreadySelected = ngModel.reduce(function(prev, next) {
							return next === childScope.rowKey ? next : prev;
						}, null);

						if (alreadySelected || alreadySelected === 0) {
							childScope.isSelected = true;
						}
					}
					var newRow = $('<tr></tr>');

                    _.forEach(attrs.$attr, function(value, key) {
                        if (key != 'wcRowRepeat') {
                            newRow.attr(value, attrs[key]);
                    	}
                    });
					newRow.append(scope.dataRowChildren.clone());

					block = {};
					block.el = newRow;
					block.scope = childScope;
					elements.push(block);

					$(noRecordRow).remove();
					parentElement.append(newRow);
					$compile(newRow)(childScope);
				}

				if(collection.length<1 && parentElement[0].children.length<1) {

					parentElement.append(noRecordRow);
				}
                element.remove();
			}
		}
	};
}]);
