/**
 * @ngdoc directive
 * @name WcResponsiveTableModule.directive:wcTableSelectionInfo
 * @restrict E
 * @element span
 * @description
 * The wcTableSelectionInfo directive is responsible for get the number of selected rows from the datatable
 */

'use strict';

angular.module('WcResponsiveTableModule').directive('wcTableSelectionInfo', function() {
	return {
		restrict: 'E',
		scope: {},
		template: '<span>{{numberOfSelectedRows}} rows currently selected.</span>',
		controller: ['$scope', 'WcTableScopeService', function($scope, WcTableScopeService) {
			var tableScope = WcTableScopeService.getTableScope();
			$scope.numberOfSelectedRows = 0;
			tableScope.$watch(function() {
				return tableScope.ngModel.length;
			}, function(newVal, oldVal) {
					if(newVal !== oldVal) {
						$scope.numberOfSelectedRows = newVal;
					}
			});
		}]
	};
});