			/**
			 * @ngdoc service
			 * @name WcResponsiveTableModule.service:wcColumnFilterService
			 * @description
			 * The wcColumnFilterService service filters the column names of the datatable on key press.
			 */

'use strict';

angular.module('WcResponsiveTableModule')
	.service('wcColumnFilterService',
		function() {
			var columnsToFilter = {};

			/**
			 * @ngdoc method
			 * @name wcColumnFilterService.getColumnsToFilter
			 * @methodOf WcResponsiveTableModule.service:wcColumnFilterService
			 * @returns {string} This string holds the list of columns to be filtered
			 * @description
			 * The method returns the columns that needs to be filtered
			 */

			this.getColumnsToFilter = function() {
				return columnsToFilter;
			};

			/**
			 * @ngdoc method
			 * @name wcColumnFilterService.setColumnsToFilter
			 * @methodOf WcResponsiveTableModule.service:wcColumnFilterService
			 * @param {Integer} key This Integer value holds the index of the array
			 * @param {string} value This string value holds the value of the array
			 * @description
			 * The method set the columns to the array
			 */

			this.setColumnsToFilter = function(key, value) {
				columnsToFilter[key] = value;
			};


		}
	);
