/**
 * @ngdoc directive
 * @name WcResponsiveTableModule.directive:wcColumnFilter
 * @restrict A
 * @scope
 * @param {string} wcColumnFilter The string of filter values of a datatable
 * @param {string} wcColumnFilterDelay The string of filter delay values of a datatable
 * @description
 * The wcColumnFilter directive adds filters to jquery datatable, this directive holds methods that are
 * related to filter operation.
 */

'use strict';

angular.module('WcResponsiveTableModule').directive('wcColumnFilter', ['$compile', function($compile) {
	return {
		restrict: 'A',
		scope: {
			wcColumnFilter: '@',
			wcColumnFilterDelay: '@'
		},
		controller: ['$scope', 'WcTableScopeService', '_', 'wcColumnFilterService', 'WcTableCollectionService', '$timeout',
			function ($scope, WcTableScopeService, _, wcColumnFilterService, WcTableCollectionService, $timeout) {
			var tableScope = WcTableScopeService.getTableScope();
			var collection = WcTableCollectionService.getCollection();

			//reassign our local collection var when an update has occurred.
			//also rerun the filters
			tableScope.$on('table.collection.update', angular.bind(this,function(){
				collection = WcTableCollectionService.getCollection();
				$timeout(angular.bind(this, function(){
					this.filterColumn();
				}), 0);
			}));

			this.predicate = $scope.wcColumnFilter;
			this.wcColumnFilterService = wcColumnFilterService;
			this.collection = collection;

			$scope.wcColumnFilterDelay = $scope.wcColumnFilterDelay || 0;
			$scope.isMultiSelect = false;

			this.filterColumn = function() {
                WcTableCollectionService.filterEnabled = true;
				//TODO only handle one copy of the collection
                var filterPairsArray = _.toPairs(wcColumnFilterService.getColumnsToFilter());
                for( var i = filterPairsArray.length-1; i >=0; i--){
					if(filterPairsArray[i][1] === ""){
						filterPairsArray.splice(i,1);
					}
				}

                if(filterPairsArray.length == 0) {
                    WcTableCollectionService.setCollection(collection, WcTableCollectionService.sortEnabled, WcTableCollectionService.filterEnabled);
				}
				else {
					var filteredCollection = _.filter(collection, angular.bind(this, function(item) {
						//TODO not the most performant....
						var match;

						for (var i = 0; i < filterPairsArray.length; i++) {
							if(angular.isArray(filterPairsArray[i][1])) {
								match = false;
								for(var j = 0; j < filterPairsArray[i][1].length; j++) {
									if(_.get(item, filterPairsArray[i][0]) && _.get(item, filterPairsArray[i][0]).toString().toLowerCase().indexOf(filterPairsArray[i][1][j].toLowerCase()) > -1) {
										match = true;
										break;
									}
								}
							}
							else {
								if(_.get(item, filterPairsArray[i][0]) && _.get(item, filterPairsArray[i][0]).toString().toLowerCase().indexOf(filterPairsArray[i][1].toLowerCase()) > -1) {
									match = true;
								} else {
									match = false;
									break;
								}
							}

						}

						return match;
					}));

					WcTableCollectionService.setCollection(filteredCollection, WcTableCollectionService.sortEnabled, WcTableCollectionService.filterEnabled);
				}
			};

			$scope.dismissFilter = function(id) {
				var filterInput = $('input[wc-column-filter="'+id+'"], select[wc-column-filter="'+id+'"]');
				if(filterInput.prop('tagName') === 'SELECT') {
					filterInput.val("");
					filterInput.trigger('change');
				}
				else {
					filterInput.val("");
					filterInput.trigger('keyup');
				}
				$(".active-filters ."+id.replace(".", "-")).remove();

			};

		}],
		link: function(scope, element, attr, ctrl) { // jshint ignore:line
			var filterListener = function() {
                var elementVal = element.val();//== null ? "" : element.val();
				var oldVal = _.get(ctrl.wcColumnFilterService.getColumnsToFilter(), ctrl.predicate);
                if (elementVal != undefined && (elementVal != "" ||  oldVal != "") && (elementVal != "" || oldVal != undefined) && elementVal!=oldVal) {
                    ctrl.wcColumnFilterService.setColumnsToFilter(ctrl.predicate, elementVal);
                    ctrl.filterColumn();
                    var filterIconFinder = $(element[0].parentElement).find('span.glyphicon-filter');

                    //in this section, we are working in the html. we can't use a dotted predicate there, as it will break the selectors.
                    //so, replace the dots with hyphens so we can still use them.
                    var htmlPredicate = ctrl.predicate.replace(".", "-");

                    //if we have a value, we know we are filtering.
                    //add or remove the heading indicator and adjust the active filter spans
                    var activeFilterButton = $(".active-filters ." + htmlPredicate);

                    if (elementVal !== "") {
                        if (!filterIconFinder.length) {
                            element.before('<span class="glyphicon glyphicon-filter"></span>');
                        }
                        if (activeFilterButton.length) {
                            activeFilterButton.find('span:last-child').text(element.parent()[0].firstChild.data + ': ' + JSON.stringify(elementVal, null, 1));
                        }
                        else {
                            $('.active-filters').append($compile('<button class="' + htmlPredicate + '" ng-click="dismissFilter(\'' + ctrl.predicate + '\')"><span class="glyphicon glyphicon-remove"></span><span>' + element.parent()[0].firstChild.data + ': ' + JSON.stringify(elementVal, null, 1) + '</span></button>')(scope));
                        }
                    }
                    else if (elementVal === "") {
                        filterIconFinder.remove();
                        activeFilterButton.remove();
                    }
                }
			};

			//if(scope.$parent.ngModel) {
				if (element.prop('tagName') === 'SELECT') {
					scope.isMultiSelect = element.attr("multiple");
					var options = (_.uniq(_.map(ctrl.collection, ctrl.predicate))).sort();

					var option = $('<option></option>');
					if(!scope.isMultiSelect) {
						option.attr('value', '');
						option.text('Select an option');
						element.append(option);
					}

					_.each(options, function (item) {
						var option = $('<option></option>');
						option.attr('value', item);
						option.text(item);
						element.append(option);
					});

					element.on('change', filterListener);
				} else {
					element.keyup(_.debounce(filterListener, scope.wcColumnFilterDelay));
				}

				//prevent sort action from firing when a click happens in a filter box
				element.on('click', function(evt){
					evt.stopPropagation();
				});
			//}
		}
	};
}]);
