/**
 * @ngdoc directive
 * @name WcResponsiveTableModule.directive:wcRowSelectRadio
 * @restrict E
 * @element div
 * @description
 * The wcRowSelectRadio directive is responsible for radio button select in datatable.
 */

'use strict';

angular.module('WcResponsiveTableModule').directive('wcRowSelectRadio', function() {
	return {
		restrict: 'E',
		template: '<input type="radio" name="table" class="wc-radio-column" ng-click="selectRow()">',
		scope: {},
		controller: ['$scope', 'WcTableScopeService', function($scope, WcTableScopeService) {
			$scope.tableScope = WcTableScopeService.getTableScope();
			$scope.rowKey = $scope.$parent.rowKey;

			$scope.selectRow = function() {
				var tableScope = WcTableScopeService.getTableScope();
				var objId = $scope.$parent.rowKey;
				tableScope.ngModel = [objId];
				tableScope.applyModel(tableScope.ngModel);
			};

			$scope.$on('table.render.complete', function() {
				if($scope.$parent.isSelected) {
					$scope.highlightRow(true);
					$scope.selectRow();
				}
			});
		}],
		link: function(scope, elem) {
			scope.highlightRow = function(shouldHighlight) {
				var row = elem.parent().parent();
				if (shouldHighlight) {
					$(row).addClass('wc-highlight-row');
				} else {
					$(row).removeClass('wc-highlight-row');
				}

				var tableCells = row.children();
				$(tableCells).each(function(index, cell) { // jshint ignore:line
					//modified this loop to make it also maintain the checked status of the table radios
					var rowRadio = $(cell).find('.wc-radio-column');
					if(shouldHighlight) {
						$(cell).addClass('wc-highlight-row');
						if(rowRadio) {
							rowRadio.prop("checked", true);
						}
					} else {
						$(cell).removeClass('wc-highlight-row');
						if(rowRadio) {
							rowRadio.prop("checked", false);
						}
					}
				});
			};
			scope.$watch('tableScope.ngModel', function(newRow) {
				if(scope.$parent.$parent.ngModel) {
					var highlight;

					highlight = newRow[0] === scope.rowKey && newRow.length > 0;

					scope.highlightRow(highlight);
				}
			});
		}
	};
});
