/**
 * @ngdoc directive
 * @name WcResponsiveTableModule.directive:wcRowSelectNone
 * @restrict A
 * @description
 * The wcRowSelectNone directive is responsible for un select all the selected rows or headers of the datatable.
 */

'use strict';

angular.module('WcResponsiveTableModule').directive('wcRowSelectNone', function() {
	return {
		restrict: 'A',
		scope: {},
		controller: ['$scope', 'WcTableScopeService', function($scope, WcTableScopeService) {
			var viewScope = WcTableScopeService.getParentScope();
			$scope.selectNone = function() {
				viewScope.$broadcast('table.select.checkbox.none');
				viewScope.$broadcast('table.select.button.none');
				$scope.$apply();
			};
		}],
		link: function(scope, elem/*, attrs, ctrl*/) {
			elem.on('click', function() {
				scope.selectNone();
			});
		}
	};
});