/**
 * @ngdoc directive
 * @name WcResponsiveTableModule.directive:wcRowSelectCheckbox
 * @restrict E
 * @element div
 * @description
 * The wcRowSelectCheckbox directive is responsible for selecting the row in datatable on a checkbox click.
 */

'use strict';

angular.module('WcResponsiveTableModule').directive('wcRowSelectCheckbox', ['$filter', function ($filter) {
	return {
		restrict: 'E',
		template: '<input type="checkbox" ng-change="selectRow()" class="wc-checkbox-column" ng-model="isChecked">',
		scope: {},
		controller: ['$scope', '$element', 'WcTableScopeService', function ($scope, $element, WcTableScopeService) { // jshint ignore:line
			$scope.selectRow = function () {
				var tableScope = WcTableScopeService.getTableScope();
				var objId = $scope.$parent.rowKey;
				var ngModel = tableScope.ngModel;
				var isInNgModel = ($filter('filter')(ngModel, objId, true)).length !== 0;

				if ((ngModel.length<1 || isInNgModel) && !$scope.isChecked) {
					tableScope.ngModel = $filter('filter')(ngModel, function (value) {
						return value !== objId;
					}, true);
				} else if (!isInNgModel) {
					tableScope.ngModel.push(objId);
				}
				tableScope.applyModel(tableScope.ngModel);
			};

			$scope.$on('table.render.complete', function () {
				if ($scope.$parent.isSelected) {
					$scope.isChecked = true;
				}
			});

			$scope.$on('table.select.checkbox.all', function () {
				$scope.isChecked = true;
				$scope.selectRow();
			});

			$scope.$on('table.select.checkbox.none', function () {
				$scope.isChecked = false;
				$scope.selectRow();
			});
		}],
		link: function (scope, elem /*, attrs, ctrl*/ ) {
			var highlightRow = function (shouldHighlight) {
				var row = elem.parent().parent();
				if (shouldHighlight) {
					$(row).addClass('wc-highlight-row');
				}
				else {
					$(row).removeClass('wc-highlight-row');
				}
				var tableCells = row.children();
				$(tableCells).each(function (index, cell) {
					// jshint unused:true
					if (shouldHighlight) {
						$(cell).addClass('wc-highlight-row');
					} else {
						$(cell).removeClass('wc-highlight-row');
					}
				});
			};

			scope.$watch('isChecked', function (boolean) {
				highlightRow(boolean);

			});
		}
	};
}]);
