		/**
		 * @ngdoc service
		 * @name WcResponsiveTableModule.service:WcTablePaginationService
		 * @requires WcTableCollectionService
		 * @description
		 * The WcTablePaginationService manages the pagination operations, this service is responsible for all pagination methods.
		 * All pagination action will come through this service.
		 */

'use strict';

angular.module('WcResponsiveTableModule')
	.service('WcTablePaginationService', ['WcTableCollectionService', function(WcTableCollectionService) {
		var paginationEnabled;

		var paginationDefaults = {
			pageStart: 0,
			pageEnd: 10,
			resultsPerPage: 10,
			numberOfPageControls: 5,
			selectResultsPerPage: [10, 25, 50, 100],
			responsiveBreakpoints: [430, 500, 600, 900],
			paginationControlsPlacement: 'both'
		};

		/**
		 * @ngdoc method
		 * @name getPaginationDefaults
		 * @methodOf WcResponsiveTableModule.service:WcTablePaginationService
		 * @returns {json} This json holds pagination default values
		 * @description
		 * This method is to get the pagination default values.
		 */

		this.getPaginationDefaults = function() {
			if (paginationEnabled) {
				return angular.copy(paginationDefaults);
			} else {
				return {
					pageStart: 0,
					pageEnd: WcTableCollectionService.getCollectionLength()
				};
			}
		};

		/**
		 * @ngdoc method
		 * @name isPaginationEnabled
		 * @methodOf WcResponsiveTableModule.service:WcTablePaginationService
		 * @returns {boolean} This boolean value returns a true or false.
		 * @description
		 * This method is to set the pagination option for datatable.
		 */

		this.isPaginationEnabled = function() {
			return paginationEnabled;
		};

		/**
		 * @ngdoc method
		 * @name setPaginationDefaults
		 * @methodOf WcResponsiveTableModule.service:WcTablePaginationService
		 * @param {json} newDefaults The json string holds the new default values for pagination
		 * @description
		 * This method is to set the pagination default values.
		 */

		this.setPaginationDefaults = function(newDefaults) {
			for(var property in newDefaults) {
				if (paginationDefaults[property] !== 0 && !paginationDefaults[property]) {
					console.error('WcTablePaginationService: setPaginationDefaults(); Property ' + property + ' is not a pagination default');
				//remember that 0 is a valid possibility
				} else if (newDefaults[property] || (typeof newDefaults[property] === 'number' && newDefaults[property] > -1)){
					paginationDefaults[property] = newDefaults[property];
				}
			}
		};

		/**
		 * @ngdoc method
		 * @name setPaginationState
		 * @methodOf WcResponsiveTableModule.service:WcTablePaginationService
		 * @param {boolean} boolean This boolean value returns a true or false.
		 * @description
		 * This method is to set the pagination enabled option for datatable
		 */

		this.setPaginationState = function(boolean) {
			paginationEnabled = boolean;
		};
	}]
);