/**
 * @ngdoc directive
 * @name WcResponsiveTableModule.directive:wcResponsiveTable
 * @scope
 * @require ngModel
 * @param {json} data This is a json string data which will be supplied to the datatable
 * @param {string} rowKey This string value holds the row key details
 * @param {object=} ngModel The model from which he data is supplied to the datatable
 * @param {Integer=} resultsPerPage This Integer value holds the number of results per page for the datatable
 * @param {object=} columnData This object holds the column data for the datatable.
 * @param {Integer=} selectResultsPerPage The Integer value that holds the number.
 * @description
 * The wcResponsiveTable directive adds responsive behavior to jquery datatable. With this directive in use, the
 * datatable will adjust automatically according to the window size.
 */

'use strict';

angular.module('WcResponsiveTableModule').directive('wcResponsiveTable', ['$compile', '_', '$timeout', '$swipe', function($compile, _, $timeout, $swipe) {
	return {
		scope: {
			data: '&',
			rowKey: '@',
			//bind between our ngModel and our interal selectedRows
			//selectedRows: '=ngModel',
			ngModel: '=',
			resultsPerPage: '=',
			paginationControlsPlacement: '@',
			responsiveView: '@',
			columnData: '=',
			selectResultsPerPage: '='
		},
		require: ['?ngModel'],
		controller: ['_', '$scope', '$element', '$attrs', 'WcTableScopeService', 'WcTableCollectionService', 'WcTablePaginationService',
			function(_, $scope, $element, $attrs, WcTableScopeService, WcTableCollectionService, WcTablePaginationService) { // jshint ignore:line
				var parentScope = $scope.$parent;

				//workaround for directive attributes that we don't want to set equal to true
				if($attrs.required === "") {
					$scope.required = true;
				}
				if($attrs.paginationEnabled === "") {
					$scope.paginationEnabled = true;
				}

				if($attrs.checkallnoneEnabled === "") {
					$scope.checkallnoneEnabled = true;
				}
                if($attrs.exporttopdfEnabled === "") {
                    $scope.exporttopdfEnabled = true;
                }

				if($attrs.exporttoexcelEnabled === ""){
					$scope.exporttoexcelEnabled = true;
				}


				WcTableCollectionService.setInitialCollection($scope.data());
				WcTableCollectionService.setRowKey($scope.rowKey);

				WcTableScopeService.setParentScope(parentScope);
				WcTableScopeService.setTableScope($scope);

				//the table errors out if the ngModel binding is not defined in the view controller
				if(!$scope.ngModel) {
					$scope.ngModel = [];
				}


				$scope.lastCollectionRendered = null;
				$scope.renderTable = function(resetToPageOne) {
					$scope.$broadcast('table.pagination.update', resetToPageOne);
					$scope.$broadcast('table.render.start');
					$scope.tableIsLoading();
				};

				$scope.tableIsLoading = function() {
					$('.wc-table-loading-indicator').show();
					$('.wc-table-loading-cover').show();
				};

				$scope.tableIsFinishedLoading = function() {
					$('.wc-table-loading-indicator').hide();
					$('.wc-table-loading-cover').hide();
				};

				$scope.tableRendered = function() {
					$('.wc-table-column-'+$scope.previousSortColumn).removeClass('wc-table-column-highlight');
					$('.wc-table-column-'+$scope.activeSortColumn).addClass('wc-table-column-highlight');

					$timeout(function(){
						parentScope.$broadcast('table.render.complete');
						//$scope.$digest();
						$scope.tableIsFinishedLoading();
					}, 0);
				};

				$scope.toggleFilterVisibility = function(){
					$('div[wc-responsive-table] thead input[wc-column-filter], div[wc-responsive-table] thead select[wc-column-filter]').toggle();
					var currentText = $('div[wc-responsive-table] .filter-visibility-toggle span:first-child').text();
					if(currentText.indexOf('Show') > -1) {
						currentText = currentText.replace('Show', 'Hide');
						$('.wc-table thead > tr > th').css('vertical-align', 'top');
					} else {
						currentText = currentText.replace('Hide', 'Show');
						$('.wc-table thead > tr > th').css('vertical-align', 'bottom');
					}
					$('div[wc-responsive-table] .filter-visibility-toggle span:first-child').text(currentText);
				};

				//should match index for the first column of data
				var ratchetIndex = 2;
				$scope.ratchetCols = function(direction) {
					//only ratchet at mobile or tablet sizes
					if(window.innerWidth < 1200) {
						$scope.$broadcast('ng-swipe');
						if(direction === 'left') {
							$('.wc-table.table-ratchet .wc-table-column-'+ratchetIndex).hide();
							if(ratchetIndex < $scope.numberOfCols) {
								ratchetIndex++;
							}
						}
						else if(direction === 'right') {
							$('.wc-table.table-ratchet .wc-table-column-'+(ratchetIndex-1)).show();
							if(ratchetIndex > 2) {
								ratchetIndex--;
							}
						}
					}
				};

				$scope.$on('ng-swipe', function() { // jshint ignore:line
					if (!$scope.animating) {
						$scope.animating = true;
						// pulse effect animation
						$('.wc-table.table-ratchet table').animate({
							opacity : 0.70
						}, 150, function() {
							$('.wc-table.table-ratchet table').animate({
								opacity : 1
							}, 200, function() {
							});
						});
						$scope.animating = false;
					}
				});

				$scope.columnsToHide = [];

				// ==========
				// Pagination
				// ==========
				if( $scope.paginationEnabled || $scope.resultsPerPage ||
					$scope.numberOfPageControls || $scope.selectResultsPerPage ||
					$scope.responsiveBreakpoints || $scope.paginationControlsPlacement) {

					WcTablePaginationService.setPaginationState(true);

					WcTablePaginationService.setPaginationDefaults({
						numberOfPageControls: $scope.numberOfPageControls,
						responsiveBreakpoints: $scope.responsiveBreakpoints,
						selectResultsPerPage: $scope.selectResultsPerPage,
						resultsPerPage: $scope.resultsPerPage,
						paginationControlsPlacement: $scope.paginationControlsPlacement
					});
				}
			}],
		link: function (scope, elem, attrs, ctrl) {
			// jshint unused:true
			var table = elem[0].children[0];
			if (table.tagName !== 'TABLE') {
				console.error('Table tag <table> must come right after <wc-responsive-table>');
				console.error('Directive found ' + table[0].tagName + ' instead');
			}

			elem.addClass('wc-table');

			var dataRow = $(table).find('tbody tr');
			var headerRow = $(table).find('thead tr')[0];

			scope.numberOfCols = headerRow.children.length;

			for (var i=0; i<headerRow.children.length; i++) {
				scope.columnsToHide[i] = false;
				$(headerRow.children[i])
					.addClass('wc-table-column-' + i);
				//.attr('column-index', i);
			}

			for (i=0; i<dataRow.children().length; i++) {
				var item = $(dataRow.children()[i]);

				item
					.addClass('wc-table-column-' + i);
				//.attr('column-index', i);

				if(scope.responsiveView === 'list') {
					var columnHeading = $(headerRow).find('th')[i];
					var columnHeadingText = $(columnHeading).clone().children().remove().end().text();
					item.attr('data-title', columnHeadingText);
				}
			}
            if(scope.exporttopdfEnabled) {
                elem.prepend($compile('<wc-export-to-pdf>')(scope));
            }		
	    	if (scope.exporttoexcelEnabled){
                elem.prepend($compile('<wc-export-to-excel>')(scope));
            }
			if (scope.paginationEnabled) {
				elem.prepend($compile('<wc-table-pagination>')(scope));
			}
			if(scope.checkallnoneEnabled) {
                elem.next().append($compile('<wc-check-all-none target-container-id="'+attrs.id+'">')(scope));
            }





			if(scope.responsiveView === 'scroll') {
				elem.addClass('table-responsive');
			}
			else if(scope.responsiveView === 'ratchet') {
				elem.addClass('table-ratchet');

				//start block of modified code coming from angular-touch library.
				var MAX_VERTICAL_DISTANCE = 75;
				// Vertical distance should not be more than a fraction of the horizontal distance.
				var MAX_VERTICAL_RATIO = 0.3;
				// At least a 30px lateral motion is necessary for a swipe.
				var MIN_HORIZONTAL_DISTANCE = 30;

				var startCoords, valid;

				var directionOfSwipe = function(coords) {
					// Check that it's within the coordinates.
					// Absolute vertical distance must be within tolerances.
					// Horizontal distance, we take the current X - the starting X.
					// This is negative for leftward swipes and positive for rightward swipes.
					// After multiplying by the direction (-1 for left, +1 for right), legal swipes
					// (ie. same direction as the directive wants) will have a positive delta and
					// illegal ones a negative delta.
					// Therefore this delta must be positive, and larger than the minimum.
					if (!startCoords) return false;
					var deltaY = Math.abs(coords.y - startCoords.y);
					var deltaX = coords.x - startCoords.x;
					if(valid && // Short circuit for already-invalidated swipes.
						deltaY < MAX_VERTICAL_DISTANCE &&
						deltaX !== 0 &&
						Math.abs(deltaX) > MIN_HORIZONTAL_DISTANCE &&
						deltaY / Math.abs(deltaX) < MAX_VERTICAL_RATIO) {
						return (deltaX > 0) ? 'right' : 'left';
					}
					else {
						return false;
					}
				};

				$swipe.bind($(table), {
					'start': function(coords) {
						startCoords = coords;
						valid = true;
					},
					'cancel': function() {
						valid = false;
					},
					'end': function(coords) {
						var direction = directionOfSwipe(coords);
						if (direction) {
							scope.ratchetCols(direction);
						}
					}
				});

				//end block of angular-touch code
			}
			else if(scope.responsiveView === 'column-toggle' || scope.responsiveView === 'radio-columns') {
				elem.addClass('column-toggle');
				//add controls to allow the user to hide columns

				var columnToggle = '<span class="dropdown column-toggle">' +
					'<span name="columnToggleDropdown" data-toggle="dropdown" id="columnToggleDropdown">' +
					'<span>Manage Columns</span><span class="glyphicon glyphicon-chevron-down"></span>' +
					'</span>' +
					'<fieldset class="dropdown-menu form-group" role="menu" aria-labelledby="columnToggleDropdown">';

				$(headerRow).children().each(function(index, item) {
					var columnName =item.firstChild.data;

					if(item.firstChild.nextSibling && item.firstChild.nextSibling.localName === 'wc-row-select-header-checkbox')
					{
						columnName='Checkbox Column' ;
					}
					if(item.firstChild.nextSibling && item.firstChild.nextSibling.localName === 'wc-row-select-header-radio')
					{

						columnName='Radio Column' ;

					}
					//var columnName = (item.firstChild.nextSibling !== null && dataname) ? dataname : item.firstChild.data;
					if( item.firstChild !== null )
					{
						columnToggle += '<div role="menuitem" class="checkbox">' +
							'<label for="col' + index + '">' +
							'<input id="col' + index + '" type="checkbox" checked ng-model="columnsToHide['+index+']"/>' + columnName +
							'</label></div>';
					}
				});

				columnToggle += '</fieldset></span>';
				scope.$watchCollection(function(){
						return scope.columnsToHide;
					}, function(newValue, oldValue) {
						if(newValue !== oldValue) {
							for(var i=0; i<newValue.length; i++) {
								if(!newValue[i]) {
									$('.wc-table.column-toggle .wc-table-column-'+i).hide();
								}
								else {
									$('.wc-table.column-toggle .wc-table-column-'+i).show();
								}
							}
						}else {
							for (var i=0; i<newValue.length; i++) {
								newValue[i] = true;
							}
							$('.form-group').click(function(e){
								e.stopPropagation();
							});
						}
					}
				);
				$(table).before($compile(columnToggle)(scope));
			}
			else if(scope.responsiveView === 'list') {
				elem.addClass('table-mobile-stack');
				$('.filter-visibility-toggle').hide();
			}
			else if(scope.responsiveView === 'drop') {
				//support dropping cols here eventually
			}

			if(elem.find('thead input[wc-column-filter], thead select[wc-column-filter]').length) {
				if(attrs.staticFilter === ""){
					scope.staticFilter = true;
					$('div[wc-responsive-table] thead input[wc-column-filter], div[wc-responsive-table] thead select[wc-column-filter]').toggle();
				}
				else  {
					var filterToggle = '<div class="filter-visibility-toggle" ng-click="toggleFilterVisibility()"><span>Show Filters</span><span class="glyphicon glyphicon-filter"></span></div>';
					$(table).before($compile(filterToggle)(scope));
					$(table).before('<div class="active-filters"></div>');
				}

			}

			var loadingElement = '<div class="wc-table-loading-cover"><div class="wc-table-loading-indicator"><span>Loading...</span></div>';

			elem.append(loadingElement);

			var setMatchingHeights = function(selector) {
				$(selector).each(function(index, item){
					$(item).css('height', $(item).closest('tr').css('height'));
				});
			};

			var columnResizeHandler = function(){
				setMatchingHeights('.wc-table-column-0');
				setMatchingHeights('.wc-table-column-1');
			};

			if(scope.responsiveView === 'scroll') {
				$(window).on('resize', _.debounce(columnResizeHandler,250));
				scope.$on('table.render.complete', _.debounce(columnResizeHandler,10));
				var scrollHandler = function(event){
					$('.table-responsive .wc-table-column-0').each(function(index, item){
						$(item).css('left', event.currentTarget.scrollLeft + "px");
					});
					$('.table-responsive .wc-table-column-1').each(function(index, item){
						$(item).css('left', (event.currentTarget.scrollLeft + 100) + "px");
					});
					$('div[wc-responsive-table].table-responsive .filter-visibility-toggle').each(function(index, item){
						$(item).css('left', (event.currentTarget.scrollLeft + 15) + "px");
					});
				};
				elem.scroll(scrollHandler);
				elem.on('touchmove', scrollHandler);
			}
			else if(scope.responsiveView === 'ratchet') {

			}
			else if(scope.responsiveView === 'list') {
				//$element.addClass('table-mobile-stack');
			}
			else if(scope.responsiveView === 'drop') {
				//support dropping cols here eventually
			}

			if(ctrl[0]) {
				ctrl[0].$name = 'wcResponsiveTable';
				//convert $viewValue to $modelValue
				//$viewValue is passed in, modify it and return what will be applied to $modelValue
				ctrl[0].$parsers.push(function(viewValue) {
					//add validation here for required fields
					if(scope.required) {
						if(!viewValue || (angular.isArray(viewValue) && viewValue.length === 0)) {
							ctrl[0].$setValidity('tableRequired', false);
						}
						else {
							ctrl[0].$setValidity('tableRequired', true);
						}
					}
					return viewValue;
				});

				//convert $modelValue to $viewValue
				//$modelValue is passed in, modify it and return what will be applied to $viewValue
				ctrl[0].$formatters.push(function(modelValue) {
					//add validation here for required fields
					if(scope.required) {
						if(!modelValue || (angular.isArray(modelValue) && modelValue.length === 0)) {
							ctrl[0].$setValidity('tableRequired', false);
						}
						else {
							ctrl[0].$setValidity('tableRequired', true);
						}
					}
					return modelValue;
				});

				//take values from the model and apply them to the view controls

				//function to call when $viewValue should be updated with a new value after a user interaction
				scope.applyModel = function(value) {
					ctrl[0].$setViewValue(value);
				};

				//register this table with the form obj
				ctrl[0].$$parentForm.$addControl(ctrl[0]);
			}

			//$compile(headerRow)(scope);

			scope.dataRowChildren = dataRow.children().clone();
			dataRow.children().remove();

			dataRow.attr('wc-row-repeat', '');
			$compile(dataRow)(scope);

			if(dataRow[0].children.length<1){
				scope.renderTable();
			}
		}
	};
} ]);
