		/**
		 * @ngdoc service
		 * @name WcResponsiveTableModule.service:WcTableCollectionService
		 * @requires WcTableScopeService
		 * @description
		 * The WcTableCollectionService manages the collection of table operations, this service is responsible to get the
		 * collection information from the table.
 */'use strict';

angular.module('WcResponsiveTableModule')
	.service('WcTableCollectionService', ['WcTableScopeService', function(WcTableScopeService) {
		var collection = [];
		var rowKey = '';

		var sortEnabled = false;
		var filterEnabled = false;

		/**
		 * @ngdoc method
		 * @name getCollection
		 * @methodOf WcResponsiveTableModule.service:WcTableCollectionService
		 * @returns {array} This array holds table collection values
		 * @description
		 * The method returns the table collection array.
		 */

		this.getCollection = function() {
			return collection;
		};

		/**
		 * @ngdoc method
		 * @name getCollectionLength
		 * @methodOf WcResponsiveTableModule.service:WcTableCollectionService
		 * @returns {Integer} This Integer value holds length of the collection array
		 * @description
		 * The method returns the collection length
		 */

		this.getCollectionLength = function() {
			return collection.length;
		};

		/**
		 * @ngdoc method
		 * @name getRowKey
		 * @methodOf WcResponsiveTableModule.service:WcTableCollectionService
		 * @returns {Integer} This Integer value holds the index of the array
		 * @description
		 * The method returns the index of the array
		 */

		this.getRowKey = function() {
			return rowKey;
		};

		/**
		 * @ngdoc method
		 * @name setCollection
		 * @methodOf WcResponsiveTableModule.service:WcTableCollectionService
		 * @param {array} newCollection This array holds the collection data
		 * @param {boolean} sorted This boolean value decides whether the collection is sorted or not
		 * @param {boolean} filtered This boolean value decides whether the collection is filtered or not
		 * @description
		 * The method set the collection to an array based on sorting and filtered value.
		 */

		this.setCollection = function(newCollection, sorted, filtered) {
			collection = newCollection;

			//need to broadcast out that our collection has changed so the filter directives can update
			if(sorted === undefined && filtered === undefined) {
				WcTableScopeService.getTableScope().$broadcast('table.collection.update');
			}

			WcTableScopeService.getTableScope().renderTable((sorted || filtered));
		};

		/**
		 * @ngdoc method
		 * @name setInitialCollection
		 * @methodOf WcResponsiveTableModule.service:WcTableCollectionService
		 * @param {array} initialCollection This array holds the collection data
		 * @description
		 * The method set the initial collection values to an array
		 */

		this.setInitialCollection = function(initialCollection) {
			collection = initialCollection;
		};

		/**
		 * @ngdoc method
		 * @name setRowKey
		 * @methodOf WcResponsiveTableModule.service:WcTableCollectionService
		 * @param {Integer} newRowKey This Integer value holds the new index of the array.
		 * @description
		 * The method set the new index to the current index value.
		 */

		this.setRowKey = function(newRowKey) {
			if (newRowKey) {
				rowKey = newRowKey;
			} else {
				rowKey = 'id';
				collection.forEach(function(obj, index) {
					obj[rowKey] = index;
				});
			}
		};
	}]
);
