/**
 * @ngdoc directive
 * @name WcResponsiveTableModule.directive:wcRowActionMultiple
 * @restrict A
 * @scope
 * @param {string} wcRowActionMultiple The parameter holds the multiple row action values
 * @description
 * The wcRowActionMultiple directive is responsible for the multiple actions that are carried out in the datatable.
 */

'use strict';

angular.module('WcResponsiveTableModule').directive('wcRowActionMultiple', function() {
	return {
		restrict: 'A',
		scope: {
			wcRowActionMultiple: '@'
		},
		controller: ['$scope', 'WcTableScopeService', function($scope, WcTableScopeService) {
			$scope.multipleAction = function() {
				var ngModel = WcTableScopeService.getTableScope().ngModel;
				var viewScope = WcTableScopeService.getParentScope();

				if(ngModel.length) {
					viewScope.$broadcast($scope.wcRowActionMultiple, [].concat(ngModel));
				}
			};
		}],
		link: function(scope, elem/*, attrs, ctrl*/) {
			elem.on('click', function() {
				scope.multipleAction();
			});
		}
	};
});